.popup {
    position: fixed;
    display: flex;
    box-sizing: border-box;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    align-items: center;
    justify-content: center;
    visibility: collapse;
    margin: auto;
    width: 100%;
    height: 100%;
    transition: all 0.3s linear;
    opacity: 0;
}